
/* APPMAIN */
h1 {
  margin-bottom: 10px;
}

.app-main {
  width: 540px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  min-height: 270px;
}

.mobile {
  /*width: 100%;*/
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.child {
  padding: 10px;
  width: 100%;
  overflow-y: scroll;
  height: 250px;
 /* background-image: url('./assets/namek.jpg');
