@font-face {
    font-family: VCR;
    src: url('./vcr.ttf');
}

.heading {
	padding: 1.2rem;
  z-index: 2;
	color: #fff;
	font-family: vcr;
	letter-spacing: 1rem;
  margin-left: 1rem;
  text-align: center;
  /* Baller code by some internet wizard */
	text-shadow:0 .01em 0px #dededa,
		0 .015em 0 #dededa,
		0 .02em  0 #dededa,
		0 .028em 0 #dededa,
		0 .035em 0 #dededa,
		0 .04em  0 #dededa,
		0 .045em 0 #dbdbd6,
		0 .055em 0 #dbdbd6,
		0 .06em  0 #dbdbd6,
		0 .065em 0 #d9d9d4,
		0 .07em  0 #d9d9d4,
		0 .08em  0 #d8d8d3,
		0 .085em 0 #d8d8d3,
		0 .09em  0 #d8d8d3,
		0 .095em 0 #d6d6d1,
		0 .105em 0 #d6d6d1,
		0 .06em .06rem rgba(104, 104, 92, 0.15),
		.07em .105em .04em rgba(0, 0, 0, .015),
		-.02em .09em .05em rgba(104, 104, 92, 0.25),
		-.03em .07em .065em rgba(104, 104, 92, 0.1),
		-.075em .08em .09em rgba(104, 104, 92, 0.06),
		-.09em .06em .195em rgba(104, 104, 92, 0.095);
  /* -------------------------------- */
}

@media (min-width: 650px) {
  .heading {
    font-size: 48px;
  }
}

@media (max-width: 650px) {
  .heading {
    font-size: 36px;
  }
}


