.titleBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  /*align-items: center;*/
  /* ------- */
  background-image: -webkit-gradient(linear, right top, left top, from(#009), to(#aaf));
  background-image: -webkit-linear-gradient(right, #009, #aaf);
  background-image: -o-linear-gradient(right, #009, #aaf);
  background-image: linear-gradient(to left, #009, #aaf);
  height: 20px;
  margin: 1px;
  padding-left: 5px;
  /* ------- */
  color: white;
  font-smoothing: none;
}

.titleBar a {
  font-family: sans-serif;
  font-weight: bold;
}

.exit {
  font-family: sans-serif;
  font-weight:bold;
  font-size: 14px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  /*------*/
  background-color: #ccc;
  height: 15px;
  width: 15px;
  border-radius: 1px;
  margin: 3px 3px 4px 0px;
}


