@fontface {
  font-family: MSSans;
  src: url(./components/assets/micross.ttf);
}

html, body {
  font-family: MSSans;
  /* -webkit-font-smoothing: none; */
}

body {
  margin: 0;
  overflow: hidden;
}

h1, h2, h3, p {
  margin: 0;
}

/* MODULAR */

.alignCentre {
  -ms-flex-align: center;
      align-items: center;
}

.spaceBetween {
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.spaceAround {
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.flexStart {
  -ms-flex-pack: start;
      justify-content: flex-start;
}

a {
  text-decoration: none;
}

hr {
  width: 100%;
}

.cr {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.cc {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
/* COMPONENTS */

.App {
  height: 100vh;
  width: 100vw;
  background-image: url('./components/assets/scanlines.png'), -webkit-gradient(linear, left top, left bottom, from(#f4c), to(#3ff));
  background-image: url('./components/assets/scanlines.png'), -webkit-linear-gradient(#f4c, #3ff);
  background-image: url('./components/assets/scanlines.png'), -o-linear-gradient(#f4c, #3ff);
  background-image: url('./components/assets/scanlines.png'), linear-gradient(#f4c, #3ff);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  background-blend-mode: overlay;
  z-index: -1;
}







.Foreground {
  position: fixed;
  top: 0;
  bottom: 0;
  left: -32px;
  right: -32px;
}


